import classNames from 'classnames';
import { keystrokesToHangul } from '../../lib/automata';

type Props = {
  focused: boolean
  guess: string
}

export const TextAutomata = ({ focused, guess }: Props) => {
  const text = keystrokesToHangul(guess)
  const classes = classNames(
    'w-24 h-9 flex item-center justify-center border-solid rounded border-2 text-xl dark:text-white mx-0.5',
    {
      'border-black dark:border-slate-100': focused && guess,
      'border-slate-200 dark:border-slate-600': !focused || !guess,
    }
  )

  return (
    <div
      className={classes}
      style={{overflow: "hidden"}}
    >
      <div>{text}</div>
    </div>
  );
}
