import * as Hangul from 'hangul-js';

export const keystrokesToHangul = (keystrokes: string) => {
  const key_to_hangul: {[key: string]: string} = {
    "4": "ㄱ",
    "44": "ㅋ",
    "444": "ㄲ",
    "4444": "ㄱ",
    "44444": "ㅋ",
    "444444": "ㄲ",
    "5": "ㄴ",
    "55": "ㄹ",
    "555": "ㄴ",
    "5555": "ㄹ",
    "55555": "ㄴ",
    "555555": "ㄹ",
    "6": "ㄷ",
    "66": "ㅌ",
    "666": "ㄸ",
    "6666": "ㄷ",
    "66666": "ㅌ",
    "666666": "ㄸ",
    "7": "ㅂ",
    "77": "ㅍ",
    "777": "ㅃ",
    "7777": "ㅂ",
    "77777": "ㅍ",
    "777777": "ㅃ",
    "8": "ㅅ",
    "88": "ㅎ",
    "888": "ㅆ",
    "8888": "ㅅ",
    "88888": "ㅎ",
    "888888": "ㅆ",
    "9": "ㅈ",
    "99": "ㅊ",
    "999": "ㅉ",
    "9999": "ㅈ",
    "99999": "ㅊ",
    "999999": "ㅉ",
    "0": "ㅇ",
    "00": "ㅁ",
    "000": "ㅇ",
    "0000": "ㅁ",
    "00000": "ㅇ",
    "000000": "ㅁ",
    "1": "ㅣ",
    "12": "ㅏ",
    "121": "ㅐ",
    "122": "ㅑ",
    "1221": "ㅒ",
    "2": "ㆍ",
    "21": "ㅓ",
    "211": "ㅔ",
    "22": "ᆢ",  // prevent ㆍㅓ
    "221": "ㅕ",
    "2211": "ㅖ",
    "23": "ㅗ",
    "223": "ㅛ",
    "3": "ㅡ",
    "32": "ㅜ",
    "322": "ㅠ",
    "3221": "ㅝ",  // not ㅠㅣ
    "32211": "ㅞ",  // not ㅝㅣ
  }
  var buf = []
  var i: number = 0
  while (i < keystrokes.length) {
    if (keystrokes[i] === "_") {
      i++;
      continue;
    }
    var k: number = 0
    do {
      k++
    } while (((i + k) < keystrokes.length) && (keystrokes.slice(i, i + k) in key_to_hangul))
    if (keystrokes.slice(i, i + k) in key_to_hangul) {
      buf.push(key_to_hangul[keystrokes.slice(i, i + k)])
      i += k
    } else {
      buf.push(key_to_hangul[keystrokes.slice(i, i + k - 1)])
      i += k - 1
    }
  }
  return Hangul.assemble(buf)
}