import { getStatuses } from '../../lib/statuses'
import { Key } from './Key'
import { useEffect } from 'react'
import { ENTER_TEXT, DELETE_TEXT } from '../../constants/strings'

type Props = {
  onChar: (value: string) => void
  onDelete: () => void
  onEnter: () => void
  guesses: string[]
  isRevealing?: boolean
}

export const Keyboard = ({
  onChar,
  onDelete,
  onEnter,
  guesses,
  isRevealing,
}: Props) => {
  const charStatuses = getStatuses(guesses)

  const onClick = (value: string) => {
    if (value === 'ENTER') {
      onEnter()
    } else if (value === 'DELETE') {
      onDelete()
    } else {
      onChar(value)
    }
  }

  useEffect(() => {
    const listener = (e: KeyboardEvent) => {
      if (e.code === 'Enter') {
        onEnter()
      } else if (e.code === 'Backspace') {
        onDelete()
      } else {
        const key = e.key.toUpperCase()
        if (key.length === 1) {
          if (key >= '0' && key <= '9') {
            onChar(key)
          } else if (key === '_') {
            onChar(key)
          } else if (key === ' ' || key === '-' || key === '.') {
            e.stopPropagation();
            onChar('_')
          }
        }
      }
    }
    window.addEventListener('keyup', listener)
    return () => {
      window.removeEventListener('keyup', listener)
    }
  }, [onEnter, onDelete, onChar])

  return (
    <div>
      <div className="flex justify-center mb-1">
        <Key value="1" onClick={onClick} status={charStatuses['1']}>1 ㅣ</Key>
        <Key value="2" onClick={onClick} status={charStatuses['2']}>2 ㆍ</Key>
        <Key value="3" onClick={onClick} status={charStatuses['3']}>3 ㅡ</Key>
        <Key width={(90 - 4) / 2} value="DELETE" onClick={onClick}>
          {DELETE_TEXT}
        </Key>
      </div>
      <div className="flex justify-center mb-1">
        <Key value="4" onClick={onClick} status={charStatuses['4']}>4<br/>ㄱㅋㄲ</Key>
        <Key value="5" onClick={onClick} status={charStatuses['5']}>5<br/>ㄴㄹ</Key>
        <Key value="6" onClick={onClick} status={charStatuses['6']}>6<br/>ㄷㅌㄸ</Key>
        <Key width={(90 - 4) / 2} value="ENTER" onClick={onClick}>
          {ENTER_TEXT}
        </Key>
      </div>
      <div className="flex justify-center mb-1">
        <Key value="7" onClick={onClick} status={charStatuses['7']}>7<br/>ㅂㅍㅃ</Key>
        <Key value="8" onClick={onClick} status={charStatuses['8']}>8<br/>ㅅㅎㅆ</Key>
        <Key value="9" onClick={onClick} status={charStatuses['9']}>9<br/>ㅈㅊㅉ</Key>
        <div className="mx-0.5" style={{width: `${(90 - 4) / 2}px`}}></div>
      </div>
      <div className="flex justify-center">
        <div className="mx-0.5" style={{width: `90px`}}></div>
        <Key value="0" onClick={onClick} status={charStatuses['0']}>0<br/>ㅇㅁ</Key>
        <Key value="_" onClick={onClick} status={charStatuses['_']} />
        <div className="mx-0.5" style={{width: `${(90 - 4) / 2}px`}}></div>
      </div>
    </div>
  )
}
